						action="file/upload"
						:limit="1"
						:multiple="true"
						:fileUrls="ruleForm.cailiaowenjian?ruleForm.cailiaowenjian:''"
						@change="cailiaowenjianUploadChange"
					></file-upload>
				</el-form-item>  
				<el-form-item :style='{"width":"50%","margin":"0 0 20px 0"}' v-else-if="ruleForm.cailiaowenjian" label="材料文件" prop="cailiaowenjian">
					<el-button :style='{"border":"0","cursor":"pointer","padding":"0 25px","margin":"0 20px 0 0","outline":"none","color":"rgba(255, 255, 255, 1)","borderRadius":"0","background":"#67D4B2","width":"auto","lineHeight":"40px","fontSize":"14px","height":"40px"}' type="text" size="small" @click="download($base.url+ruleForm.cailiaowenjian)">下载</el-button>
				</el-form-item>
				<el-form-item :style='{"width":"50%","margin":"0 0 20px 0"}' v-else-if="!ruleForm.cailiaowenjian" label="材料文件" prop="cailiaowenjian">
					<el-button :style='{"border":"0","cursor":"pointer","padding":"0 25px","margin":"0 20px 0 0","outline":"none","color":"rgba(255, 255, 255, 1)","borderRadius":"0","background":"#67D4B2","width":"auto","lineHeight":"40px","fontSize":"14px","height":"40px"}' type="text" size="small">无</el-button>
				</el-form-item>
				<el-form-item :style='{"width":"50%","margin":"0 0 20px 0"}' class="input" v-if="type!='info'"  label="供应商账号" prop="gongyingshangzhanghao">
					<el-input v-model="ruleForm.gongyingshangzhanghao" placeholder="供应商账号" clearable  :readonly="ro.gongyingshangzhanghao"></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"50%","margin":"0 0 20px 0"}' v-else class="input" label="供应商账号" prop="gongyingshangzhanghao">
					<el-input v-model="ruleForm.gongyingshangzhanghao" placeholder="供应商账号" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"50%","margin":"0 0 20px 0"}' class="input" v-if="type!='info'"  label="供应商名称" prop="gongyingshangmingcheng">
					<el-input v-model="ruleForm.gongyingshangmingcheng" placeholder="供应商名称" clearable  :readonly="ro.gongyingshangmingcheng"></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"50%","margin":"0 0 20px 0"}' v-else class="input" label="供应商名称" prop="gongyingshangmingcheng">
					<el-input v-model="ruleForm.gongyingshangmingcheng" placeholder="供应商名称" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"50%","margin":"0 0 20px 0"}' class="input" v-if="type!='info'"  label="联系电话" prop="lianxidianhua">
					<el-input v-model="ruleForm.lianxidianhua" placeholder="联系电话" clearable  :readonly="ro.lianxidianhua"></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"50%","margin":"0 0 20px 0"}' v-else class="input" label="联系电话" prop="lianxidianhua">
					<el-input v-model="ruleForm.lianxidianhua" placeholder="联系电话" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"50%","margin":"0 0 20px 0"}' class="date" v-if="type!='info'" label="时间" prop="shijian">
					<el-date-picker
						value-format="yyyy-MM-dd HH:mm:ss"
						v-model="ruleForm.shijian" 
						type="datetime"
						:readonly="ro.shijian"
						placeholder="时间"
					></el-date-picker>
				</el-form-item>
				<el-form-item :style='{"width":"50%","margin":"0 0 20px 0"}' class="input" v-else-if="ruleForm.shijian" label="时间" prop="shijian">
					<el-input v-model="ruleForm.shijian" placeholder="时间" readonly></el-input>
				</el-form-item>
			</template>
			<el-form-item :style='{"width":"100%","padding":"0","margin":"0"}' class="btn">
				<el-button :style='{"border":"0","cursor":"pointer","padding":"0","margin":"0 20px 0 0","outline":"none","color":"rgba(255, 255, 255, 1)","borderRadius":"0","background":"#67D4B2","width":"128px","lineHeight":"40px","fontSize":"14px","height":"40px"}'  v-if="type!='info'" type="primary" class="btn-success" @click="onSubmit">提交</el-button>
				<el-button :style='{"border":"0px solid rgba(64, 158, 255, 1)","cursor":"pointer","padding":"0","margin":"0","outline":"none","color":"#fff","borderRadius":"0","background":"#67D4B2","width":"128px","lineHeight":"40px","fontSize":"14px","height":"40px"}' v-if="type!='info'" class="btn-close" @click="back()">取消</el-button>
				<el-button :style='{"border":"0px solid rgba(64, 158, 255, 1)","cursor":"pointer","padding":"0","margin":"0","outline":"none","color":"#fff","borderRadius":"0","background":"#67D4B2","width":"128px","lineHeight":"40px","fontSize":"14px","height":"40px"}' v-if="type=='info'" class="btn-close" @click="back()">返回</el-button>
			</el-form-item>
		</el-form>
    

  </div>
</template>
<script>
// 数字，邮件，手机，url，身份证校验
import { isNumber,isIntNumer,isEmail,isPhone, isMobile,isURL,checkIdCard } from "@/utils/validate";
export default {
	data() {
		let self = this
		var validateIdCard = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!checkIdCard(value)) {
				callback(new Error("请输入正确的身份证号码"));
			} else {
				callback();
			}
		};
		var validateUrl = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isURL(value)) {
				callback(new Error("请输入正确的URL地址"));
			} else {
				callback();
			}
		};
		var validateMobile = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isMobile(value)) {
				callback(new Error("请输入正确的手机号码"));
			} else {
				callback();
			}
		};
		var validatePhone = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isPhone(value)) {
				callback(new Error("请输入正确的电话号码"));
			} else {
				callback();
			}
		};
		var validateEmail = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isEmail(value)) {
				callback(new Error("请输入正确的邮箱地址"));
			} else {
				callback();
			}
		};
		var validateNumber = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isNumber(value)) {
				callback(new Error("请输入数字"));
			} else {
				callback();
			}
		};
		var validateIntNumber = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isIntNumer(value)) {
				callback(new Error("请输入整数"));
			} else {
				callback();
			}
		};
		return {
			id: '',
			type: '',
			
			
			ro:{
				gonggaobiaoti : false,
				zhaobiaoxiangmu : false,
				yiyuanzhanghao : false,
				yiyuanmingcheng : false,
				yiyuandianhua : false,
				cailiaowenjian : false,
				gongyingshangzhanghao : false,
				gongyingshangmingcheng : false,
				lianxidianhua : false,
				shijian : false,
				sfsh : false,
				shhf : false,
			},
			
			
			ruleForm: {
				gonggaobiaoti: '',
				zhaobiaoxiangmu: '',
				yiyuanzhanghao: '',
				yiyuanmingcheng: '',
				yiyuandianhua: '',
				cailiaowenjian: '',
				gongyingshangzhanghao: '',
				gongyingshangmingcheng: '',
				lianxidianhua: '',
				shijian: '',
				shhf: '',
			},
		
			
			rules: {
				gonggaobiaoti: [
				],
				zhaobiaoxiangmu: [
				],
				yiyuanzhanghao: [
				],
				yiyuanmingcheng: [
				],
				yiyuandianhua: [
				],
				cailiaowenjian: [
				],
				gongyingshangzhanghao: [
				],
				gongyingshangmingcheng: [
				],
				lianxidianhua: [
				],
				shijian: [
				],
				sfsh: [
				],
				shhf: [
				],
			}
		};
	},
	props: ["parent"],
	computed: {



	},
	created() {
		this.ruleForm.shijian = this.getCurDateTime()
	},
	methods: {
		
		// 下载
		download(file){
			window.open(`${file}`)
		},
		// 初始化
		init(id,type) {
			if (id) {
				this.id = id;
				this.type = type;
			}
			if(this.type=='info'||this.type=='else'){
				this.info(id);
			}else if(this.type=='logistics'){
				this.logistics=false;
				this.info(id);
			}else if(this.type=='cross'){
				var obj = this.$storage.getObj('crossObj');
				for (var o in obj){
						if(o=='gonggaobiaoti'){
							this.ruleForm.gonggaobiaoti = obj[o];
							this.ro.gonggaobiaoti = true;
							continue;
						}
						if(o=='zhaobiaoxiangmu'){
							this.ruleForm.zhaobiaoxiangmu = obj[o];
							this.ro.zhaobiaoxiangmu = true;
							continue;
						}
						if(o=='yiyuanzhanghao'){
							this.ruleForm.yiyuanzhanghao = obj[o];
							this.ro.yiyuanzhanghao = true;
							continue;
						}
						if(o=='yiyuanmingcheng'){
							this.ruleForm.yiyuanmingcheng = obj[o];
							this.ro.yiyuanmingcheng = true;
							continue;
						}
						if(o=='yiyuandianhua'){
							this.ruleForm.yiyuandianhua = obj[o];
							this.ro.yiyuandianhua = true;
							continue;
						}
						if(o=='cailiaowenjian'){
							this.ruleForm.cailiaowenjian = obj[o];
							this.ro.cailiaowenjian = true;
							continue;
						}
						if(o=='gongyingshangzhanghao'){
							this.ruleForm.gongyingshangzhanghao = obj[o];
							this.ro.gongyingshangzhanghao = true;
							continue;
						}
						if(o=='gongyingshangmingcheng'){
							this.ruleForm.gongyingshangmingcheng = obj[o];
							this.ro.gongyingshangmingcheng = true;
							continue;
						}
						if(o=='lianxidianhua'){
							this.ruleForm.lianxidianhua = obj[o];
							this.ro.lianxidianhua = true;
							continue;
						}
						if(o=='shijian'){
							this.ruleForm.shijian = obj[o];
							this.ro.shijian = true;
							continue;
						}
				}
				












			}
			
			
			// 获取用户信息
			this.$http({
				url: `${this.$storage.get('sessionTable')}/session`,
				method: "get"
			}).then(({ data }) => {
				if (data && data.code === 0) {
					
					var json = data.data;
					if(json.gongyingshangzhanghao!=''&&json.gongyingshangzhanghao && this.$storage.get("role")!="管理员"){
						this.ruleForm.gongyingshangzhanghao = json.gongyingshangzhanghao
						this.ro.gongyingshangzhanghao = true;
					}
					if(json.gongyingshangmingcheng!=''&&json.gongyingshangmingcheng && this.$storage.get("role")!="管理员"){
						this.ruleForm.gongyingshangmingcheng = json.gongyingshangmingcheng
						this.ro.gongyingshangmingcheng = true;
					}
					if(json.lianxidianhua!=''&&json.lianxidianhua && this.$storage.get("role")!="管理员"){
						this.ruleForm.lianxidianhua = json.lianxidianhua
						this.ro.lianxidianhua = true;
					}
				} else {
					this.$message.error(data.msg);
				}
			});
			
			
		},
    // 多级联动参数

    info(id) {
      this.$http({
        url: `zhaobiaoxinxi/info/${id}`,
        method: "get"
      }).then(({ data }) => {
        if (data && data.code === 0) {
        this.ruleForm = data.data;
        //解决前台上传图片后台不显示的问题
        let reg=new RegExp('../../../upload','g')//g代表全部
        } else {
          this.$message.error(data.msg);
        }
      });
    },


    // 提交
    onSubmit() {












	if(this.ruleForm.cailiaowenjian!=null) {
		this.ruleForm.cailiaowenjian = this.ruleForm.cailiaowenjian.replace(new RegExp(this.$base.url,"g"),"");
	}













var objcross = this.$storage.getObj('crossObj');

      //更新跨表属性
       var crossuserid;
       var crossrefid;
       var crossoptnum;
       if(this.type=='cross'){
                var statusColumnName = this.$storage.get('statusColumnName');
                var statusColumnValue = this.$storage.get('statusColumnValue');
                if(statusColumnName!='') {
                        var obj = this.$storage.getObj('crossObj');
                       if(statusColumnName && !statusColumnName.startsWith("[")) {
                               for (var o in obj){
                                 if(o==statusColumnName){
                                   obj[o] = statusColumnValue;
                                 }
                               }
                               var table = this.$storage.get('crossTable');
                             this.$http({
                                 url: `${table}/update`,
                                 method: "post",
                                 data: obj
                               }).then(({ data }) => {});
                       } else {
                               crossuserid=this.$storage.get('userid');
                               crossrefid=obj['id'];
                               crossoptnum=this.$storage.get('statusColumnName');
                               crossoptnum=crossoptnum.replace(/\[/,"").replace(/\]/,"");
                        }
                }
        }
       this.$refs["ruleForm"].validate(valid => {
         if (valid) {
		 if(crossrefid && crossuserid) {
			 this.ruleForm.crossuserid = crossuserid;
			 this.ruleForm.crossrefid = crossrefid;
			let params = { 
				page: 1, 
				limit: 10, 
				crossuserid:this.ruleForm.crossuserid,
				crossrefid:this.ruleForm.crossrefid,
			} 
			this.$http({ 
				url: "zhaobiaoxinxi/page", 
				method: "get", 
				params: params 
			}).then(({ 
				data 
			}) => { 
				if (data && data.code === 0) { 
				       if(data.data.total>=crossoptnum) {
					     this.$message.error(this.$storage.get('tips'));
					       return false;
				       } else {
					 this.$http({
					   url: `zhaobiaoxinxi/${!this.ruleForm.id ? "save" : "update"}`,
					   method: "post",
					   data: this.ruleForm
					 }).then(({ data }) => {
					   if (data && data.code === 0) {
					     this.$message({
					       message: "操作成功",
					       type: "success",
					       duration: 1500,
					       onClose: () => {
						 this.parent.showFlag = true;
						 this.parent.addOrUpdateFlag = false;
						 this.parent.zhaobiaoxinxiCrossAddOrUpdateFlag = false;
						 this.parent.search();
						 this.parent.contentStyleChange();
					       }
					     });
					   } else {
					     this.$message.error(data.msg);
					   }
					 });

				       }
				} else { 
				} 
			});
		 } else {
			 this.$http({
			   url: `zhaobiaoxinxi/${!this.ruleForm.id ? "save" : "update"}`,
			   method: "post",
			   data: this.ruleForm
			 }).then(({ data }) => {
			   if (data && data.code === 0) {
			     this.$message({
			       message: "操作成功",
			       type: "success",
			       duration: 1500,
			       onClose: () => {
				 this.parent.showFlag = true;
				 this.parent.addOrUpdateFlag = false;
				 this.parent.zhaobiaoxinxiCrossAddOrUpdateFlag = false;
				 this.parent.search();
				 this.parent.contentStyleChange();
			       }
			     });
			   } else {
			     this.$message.error(data.msg);
			   }
			 });
		 }
         }
       });
    },
    // 获取uuid
    getUUID () {
      return new Date().getTime();
    },
    // 返回
    back() {
      this.parent.showFlag = true;
      this.parent.addOrUpdateFlag = false;
      this.parent.zhaobiaoxinxiCrossAddOrUpdateFlag = false;
      this.parent.contentStyleChange();
    },
    cailiaowenjianUploadChange(fileUrls) {
	    this.ruleForm.cailiaowenjian = fileUrls;
    },
  }
};
</script>
<style lang="scss" scoped>
